* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: "montserrat", sans-serif;
  /* background: #1d1f30; */
  background-color: #19181f;
}

header {
  padding: 50px 15px 0px;
}

header h1 {
  color: #fe4141;
  font-size: 48px;
  font-weight: 500;
  text-align: center;
}

main {
  width: 100%;
  max-width: 960px;
  margin: 0 auto;
}

section {
  padding: 15px;
}

.searchbox {
  display: block;
  width: 100%;
  padding: 15px;

  border: none;
  outline: none;
  background: none;

  background-color: #eee;
  border-radius: 8px;

  color: #53565a;
  font-size: 20px;
  font-weight: 300;

  transition: 0.4s ease-out;
}

.searchbox:focus {
  box-shadow: 0px 0px 2px 1px #fe4141;
}

.results {
  display: flex;
  flex-wrap: wrap;
  margin: 0px -15px;
}

.results .result {
  width: 25%;
  min-width: 230px;
  padding: 15px;
  display: flex;
  flex-direction: column;
  cursor: pointer;
}

.results .result img {
  display: block;
  width: 100%;
  height: 264px;
  border-radius: 8px;
  -webkit-box-shadow: 2px 0px 7px 0px rgba(136, 133, 153, 1);
  -moz-box-shadow: 2px 0px 7px 0px rgba(136, 133, 153, 1);
  box-shadow: 2px 0px 7px 0px rgba(136, 133, 153, 1);
}

.results .result h3 {
  top: 20px;
  left: 20px;
  text-transform: uppercase;
  color: #fff;
  background: rgba(25, 25, 25, 0.3);
  padding: 10px;
  font-size: 13px;
}

.results .result:hover h3 {
  font-weight: 700;
}

.popup {
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  background-color: #222128;

  overflow-y: scroll;
}

.popup .content {
  display: block;
  width: 100%;
  max-width: 960px;
  margin: 0 auto;
  padding: 25px;

  color: #fff;
}

.popup .content h2 {
  font-size: 28px;
  font-weight: 600;
}
.popup .content h2 span {
  font-weight: 300;
  font-size: 20px;
}

.popup .content .rating {
  margin-bottom: 30px;
}

.popup .content .plot {
  flex-wrap: wrap;
  margin: 0px -15px 30px;
}

.popup .content .plot img {
  flex: 1 1 50%;
  max-width: 300px;
  padding: 10px 15px 0 15px;
}

.popup .content .plot p {
  flex: 1 1 50%;
  padding: 15px;
}
h5 {
  padding: 15px 15px 0 15px;
}

.popup .content .close {
  display: inline-block;
  padding: 7px 18px;
  font-size: 18px;
  font-weight: 700;
  background: #d23232;
  color: #fff;
  border-radius: 8px;
  border: none;
  outline: none;
  -webkit-appearance: none;
  appearance: none;
  cursor: pointer;
  transition: 0.4s ease-out;
}

.popup .content .close:hover {
  background: #223343;
}
.fa-star {
  color: #ffd700;
  padding: 0 10px;
  font-size: 12px;
}
span {
  color: #aeb1b6;
  font-size: 0.9em;
}
.movie-gen li,
.label {
  font-family: "Open Sans", sans-serif;
  font-size: 12px;
  color: #acacac;
  width: auto;
  display: block;
  float: left;
  margin-right: 6px;
  font-weight: 600;
  display: block;
}
.col_1 {
  margin: 0% 0.5% 0.5% 0.5%;
  display: block;
}

.year {
  float: right;
  padding: 0 12px 0 0;
}
@media only screen and (max-width: 768px) {
  /* For mobile phones: */
  .results .result {
    margin: auto;
  }
}
footer {
  display: flex;

  height: 80px;
  text-align: center;
  background: #222128;
  align-items: center;
  justify-content: space-between;
  margin: auto;
}

footer a,
footer p {
  color: #eee;
  font: 20px;
  text-decoration: none;
}
footer a:hover {
  text-decoration: none;
  color: #fe4141;
}
footer p {
  margin-left: 30px;
}
.git {
  margin-right: 30px;
}
